import React, { useState, useEffect, useCallback } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';
import Card from '../component/Card';

const Searchpage = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [query, setQuery] = useState(new URLSearchParams(location.search).get('q') || '');

  // Fetch data from API
  const fetchData = useCallback(async () => {
    if (!query) return; // Prevent fetch if query is empty

    setLoading(true);
    setError(null); // Reset error before fetching

    try {
      const response = await axios.get(`/search/multi`, {
        params: {
          query,
          page: 1
        }
      });
      setData(response.data.results);
    } catch (error) {
      setError("Error fetching search data.");
      console.error("Error fetching search data:", error);
    } finally {
      setLoading(false);
    }
  }, [query]);

  // Fetch data when the query changes
  useEffect(() => {
    fetchData();
  }, [fetchData]);

  const handleSearchChange = (e) => {
    const newQuery = e.target.value;
    setQuery(newQuery);
    navigate(`/search?q=${newQuery}`);
  };

  return (
    <div className='py-16'>
      <div className='lg:hidden mx-1 my-2 sticky top-[70px] z-30'>
        <input
          type='text'
          placeholder='Search here...'
          value={query}
          onChange={handleSearchChange}
          className="px-4 py-1 bg-white text-lg w-full rounded-full text-neutral-900"
        />
      </div>
      <div className='container mx-auto'>
        <h3 className='capitalize text-lg lg:text-xl font-semibold my-3'>Search Results</h3>
        {loading && <p>Loading...</p>}
        {error && <p className="text-red-500">{error}</p>}
        <div className='grid grid-cols-[repeat(auto-fit,230px)] gap-6 justify-center lg:justify-start'>
          {data.map((searchData) => (
            <Card
              data={searchData}
              key={searchData.id + "search"}
              media_type={searchData.media_type}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

export default Searchpage;
